.wpte-product-container{
    .wpte-product-caption-hover-style-1 {
        overflow: hidden;
    }
    .wpte-product-caption-hover-style-1,
    .wpte-product-caption-hover-style-1:before,
    .wpte-product-caption-hover-style-1:after,
    .wpte-product-caption-hover-style-1{
        .wpte-product-hover-figure,
        .wpte-product-hover-figure:before,
        .wpte-product-hover-figure:after,
        .wpte-product-hover-figure-caption,
        .wpte-product-hover-figure-caption:before,
        .wpte-product-hover-figure-caption:after {
            background-color: hsla(122, 78%, 39%, 0.92);
        }
    }

    [class^='wpte-product-blinds']:before,
    [class^='wpte-product-blinds']:after,
    [class^='wpte-product-blinds'] .wpte-product-hover-figure-caption:before,
    [class^='wpte-product-blinds'] .wpte-product-hover-figure-caption:after,
    [class*=' wpte-product-blinds']:before,
    [class*=' wpte-product-blinds']:after,
    [class*=' wpte-product-blinds'] .wpte-product-hover-figure-caption:before,
    [class*=' wpte-product-blinds'] .wpte-product-hover-figure-caption:after {
        opacity: 0;
        position: absolute;
        content: '';
    }

    [class^='wpte-product-blinds']:before,
    [class*=' wpte-product-blinds']:before {
        top: 0;
        transition-delay: 0s;
        z-index: 2;
    }

    [class^='wpte-product-blinds']:after,
    [class*=' wpte-product-blinds']:after {
        top: 25%;
        transition-delay: 0.105s;
        z-index: 2;
    }

    [class^='wpte-product-blinds'],
    [class*=' wpte-product-blinds'] {
        .wpte-product-hover-figure-caption {
            z-index: 3;
            background-color: transparent !important;
        }
        .wpte-product-hover-figure-caption * {
            opacity: 0;
            transition-delay: 0s;
        }
    }


    [class^='wpte-product-blinds'] .wpte-product-hover-figure-caption:before,
    [class^='wpte-product-blinds'] .wpte-product-hover-figure-caption:after,
    [class*=' wpte-product-blinds'] .wpte-product-hover-figure-caption:before,
    [class*=' wpte-product-blinds'] .wpte-product-hover-figure-caption:after {
        z-index: -1;
    }

    [class^='wpte-product-blinds']:hover:before,
    [class^='wpte-product-blinds']:hover:after,
    [class^='wpte-product-blinds']:hover .wpte-product-hover-figure-caption:before,
    [class^='wpte-product-blinds']:hover .wpte-product-hover-figure-caption:after,
    [class*=' wpte-product-blinds']:hover:before,
    [class*=' wpte-product-blinds']:hover:after,
    [class*=' wpte-product-blinds']:hover .wpte-product-hover-figure-caption:before,
    [class*=' wpte-product-blinds']:hover .wpte-product-hover-figure-caption:after{
        transform: scale(1);
        opacity: 1;
    }

    [class^='wpte-product-blinds']:hover .wpte-product-hover-figure-caption *,
    [class*=' wpte-product-blinds']:hover .wpte-product-hover-figure-caption *{
        opacity: 1;
        transition-delay: 0.4025s;
    }

    .wpte-product-blinds-horizontal:before,
    .wpte-product-blinds-horizontal:after,
    .wpte-product-blinds-horizontal .wpte-product-hover-figure-caption:before,
    .wpte-product-blinds-horizontal .wpte-product-hover-figure-caption:after {
        height: 25%;
        width: 100%;
        transform: scaleY(0);
        left: 0;
    }

    .wpte-product-blinds-horizontal:before {
        top: 0;
    }
    
    .wpte-product-blinds-horizontal:after {
        top: 25%;
    }
    
    .wpte-product-blinds-horizontal{
        .wpte-product-hover-figure-caption:before {
            top: 50%;
        }
        .wpte-product-hover-figure-caption:after {
            top: 75%;
        }
    }

    .wpte-product-blinds-horizontal:hover{
        .wpte-product-hover-figure-caption *{
            transition-delay: 0.35s;
        }
    }

    .wpte-product-blinds-vertical:before,
    .wpte-product-blinds-vertical:after,
    .wpte-product-blinds-vertical .wpte-product-hover-figure-caption:before,
    .wpte-product-blinds-vertical .wpte-product-hover-figure-caption:after {
        width: 25%;
        height: 100%;
        transform: scaleX(0);
        top: 0;
    }

    .wpte-product-blinds-vertical:before {
        left: 0;
    }
    
    .wpte-product-blinds-vertical:after {
        left: 25%;
    }
    
    .wpte-product-blinds-vertical{
        .wpte-product-hover-figure-caption:before {
            left: 50%;
        }
        .wpte-product-hover-figure-caption:after {
            left: 75%;
        }
    }

    .wpte-product-blinds-vertical:hover{
        .wpte-product-hover-figure-caption * {
            transition-delay: 0.35s;
        }
    }

    .wpte-product-blinds-up:before,
    .wpte-product-blinds-up:after,
    .wpte-product-blinds-up .wpte-product-hover-figure-caption:before,
    .wpte-product-blinds-up .wpte-product-hover-figure-caption:after {
        height: 25%;
        width: 100%;
        transform: scaleY(0);
        left: 0;
    }

    .wpte-product-blinds-up:before {
        top: 0;
        transition-delay: 0.35s;
    }
    
    .wpte-product-blinds-up:after {
        top: 25%;
        transition-delay: 0.21s;
    }

    .wpte-product-blinds-up{
        .wpte-product-hover-figure-caption:before {
            top: 50%;
            transition-delay: 0.105s;
        }
        .wpte-product-hover-figure-caption:after {
            top: 75%;
            transition-delay: 0s;
        }
    }

    .wpte-product-blinds-down:before,
    .wpte-product-blinds-down:after,
    .wpte-product-blinds-down .wpte-product-hover-figure-caption:before,
    .wpte-product-blinds-down .wpte-product-hover-figure-caption:after {
        height: 25%;
        width: 100%;
        transform: scaleY(0);
        left: 0;
    }

    .wpte-product-container .wpte-product-blinds-down:before {
        top: 0;
        transition-delay: 0s;
    }
    
    .wpte-product-blinds-down:after {
        top: 25%;
        transition-delay: 0.105s;
    }
    
    .wpte-product-blinds-down{
        .wpte-product-hover-figure-caption:before {
            top: 50%;
            transition-delay: 0.21s;
        }
        .wpte-product-hover-figure-caption:after {
            top: 75%;
            transition-delay: 0.35s;
        }
    }

    .wpte-product-blinds-left:before,
    .wpte-product-blinds-left:after,
    .wpte-product-blinds-left .wpte-product-hover-figure-caption:before,
    .wpte-product-blinds-left .wpte-product-hover-figure-caption:after {
        width: 25%;
        height: 100%;
        transform: scaleX(0);
        top: 0;
    }

    .wpte-product-blinds-left:before {
        left: 0;
        transition-delay: 0.35s;
    }

    .wpte-product-blinds-left:after {
        left: 25%;
        transition-delay: 0.21s;
    }

    
    .wpte-product-blinds-left{
        .wpte-product-hover-figure-caption:before {
            left: 50%;
            transition-delay: 0.105s;
        }
        .wpte-product-hover-figure-caption:after {
            left: 75%;
            transition-delay: 0s;
        }
    }

    .wpte-product-blinds-right:before,
    .wpte-product-blinds-right:after,
    .wpte-product-blinds-right .wpte-product-hover-figure-caption:before,
    .wpte-product-blinds-right .wpte-product-hover-figure-caption:after {
        width: 25%;
        height: 100%;
        transform: scaleX(0);
        top: 0;
    }

    .wpte-product-blinds-right:before {
        left: 0;
        transition-delay: 0s;
    }

    .wpte-product-blinds-right:after {
        left: 25%;
        transition-delay: 0.105s;
    }

    .wpte-product-blinds-right{
        .wpte-product-hover-figure-caption:before {
            left: 50%;
            transition-delay: 0.21s;
        }
        .wpte-product-hover-figure-caption:after {
            left: 75%;
            transition-delay: 0.35s;
        }
    }

}
